@font-face {
    font-family: 'Bubblegum Sans';
    font-style: normal;
    font-weight: 400;
    src: url(https://fonts.gstatic.com/s/bubblegumsans/v9/AYCSpXb_Z9EORv1M5QTjEzMEteaOxIL_bw.woff2) format('woff2');
    unicode-range: U+0100-024F, U+0259, U+1E00-1EFF, U+2020, U+20A0-20AB, U+20AD-20CF, U+2113, U+2C60-2C7F, U+A720-A7FF;
}

.round1 .page1 {
    font-family: 'Bubblegum Sans';
    background-color: #120202;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
}
.round1 .page1 p{
    font-size: 25px;
    font-family: 'Bubblegum Sans';
    color: antiquewhite;
}
.round1 .page1 .desc{
    padding: 300px;
}