.round1 .page7 {
    background-color: #282c34;
    color: white;
    height: 100%;
    padding-top: 1px;
    /* padding-right: 0; */
    padding: auto;
    
    /* width: 100%; */
    /* padding: 1px 100px 0 100px; */
    /* text-align: center; */
