}

.round1 .page7 p{
    margin: 0;
    padding: 0;
}

.round1 .page7 .header {
    margin-top: 100px;
    /* height: 100px; */
}

.round1 .page7 .block {
    position: relative;
    display: inline-block;
    margin: 7px;
    height: 120px;
    width: 120px;
    text-align: center;
    background-color: rgba(255, 255, 255, 0.2) !important;
    color: white !important;
}

.round1 .page7 .empty {
    background-color: transparent !important;
    /* border-radius: 50% !important; */
}

.round1 .page7 .block p {
    /* margin: 0; */
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.round1 .page7 .helpIcon {
    cursor: pointer;
}

.round1 .page7 .key {
    margin-top: 70px;;
    text-align: center;
}

.round1 .page7 .keyInput {
    margin: 0 20px;
    width: 400px;
    border: none;
    outline: none;
    border-bottom: 1px solid #fff;
    background-color: transparent;
    color: white;
    text-align: center;
    font-size: 15px;
    padding: 8px;
}