    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
}

.round2 .page6 .div{
    /* background-color: rgba(0, 0, 0,0.4); */
    padding-top: 150px;
    padding-bottom: 300px;
    /* padding-left: 200px; */
    /* padding-right: 200px; */
