.round1 .page5 {
    background-color: #212121;
    color: white;
    padding-top: 1px;
    width: 100%;
}

.round1 .page5 p{
    margin: 0;
    padding: 3px;
}

.round1 .page5 a {
    color: rgb(0, 132, 255);
}

.round1 .page5 .header {
    margin-top: 60px;
    /* height: 100px; */
}

.round1 .page5 .block {
    position: relative;
    display: inline-block;
    margin: 7px;
    height: 60px;
    width: 800px;
    text-align: center;
    background-color: rgba(255, 255, 255, 0.2) !important;
    color: black !important;
}

.round1 .page5 .empty {
    background-color: transparent !important;
    /* border-radius: 50% !important; */
}

.round1 .page5 .block p {
    /* margin: 0; */
    color: white;
    position: absolute;
    /* width: 500px; */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.round1 .page5 .helpIcon {
    cursor: pointer;
}

.round1 .page5 .key {
    margin-top: 70px;
    text-align: center;
}

.round1 .page5 .keyInput {
    margin: 0 20px;
    width: 400px;
    border: none;
    outline: none;
    border-bottom: 1px solid #fff;
    background-color: transparent;
    color: white;
    text-align: center;
    font-size: 15px;
    padding: 8px;
} 