.round1 .page8 {
    position: relative;
    background-color: #282c34;
    color: white;
    height: 100%;
    padding-top: 1px;
    width: 100%;
}

.round1 .page8 .instructions {
    text-align: center;
    margin-top: 30px;
}

.round1 .page8 .values {
    width: 100%;
    text-align: center;
}

.round1 .page8 .result_box {
    display: inline-block;;
    margin-top: 40px;
    /* float: left; */
    margin: 40px;
}

.round1 .page8 .result_box .title {
    font-size: 30px;
    margin-bottom: 10px;
}

.round1 .page8 .op_title {
    font-size: 23px;
    text-align: center;
    margin-bottom: 10px;
}

.round1 .page8 .operations {
    text-align: center;
    /* margin-top: 30px; */
}
.round1 .page8 svg {
    border-radius: 50%;
}

.round1 .page8 .circle_disabled {
    /* position: absolute; */
    border-radius: 60%;
    border: 0px solid !important;
    /* text-align: center; */
    height: 176px !important;
    width: 180px !important;
    /* left: 44%; */
    /* top: 47%; */
    z-index: 10;
    background-color: transparent !important;
    cursor: default !important;
    /* transform: translate(); */
    transform: rotate(65.5deg) translate(-7.8em) rotate(-90deg);
}

.round1 .page8 .operations .op {
    width: 76px;
    height: 76px;
    border-radius: 60%;
    border: 1px solid;
    text-align: center;
}

.round1 .page8 .operations .op:hover {
    background-color: rgb(67, 150, 67);
    cursor: pointer;
}

.round1 .page8 .operations .op p {
    margin: 0;
    padding: 0;
}

.round1 .page8 .circle-container {
    margin: 15.75em auto 0;
}
.round1 .page8 .circle-container .op {
    position: absolute;
    left: 50%;
    margin: -2.5em;
}

.round1 .page8 .bc {
    transform: rotate(285deg) translate(3em) rotate(-285deg); 
    width: 50px !important;
    height: 50px !important;
    border-radius: 60%;
    /* border: 1px solid; */
    /* text-align: center; */
}
