.round2 .page7 {
    text-align: center;
    background-image: url('./bck7.jpg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
}

.round2 .page7 div{
    background-color: rgba(0, 0, 0,0.6);
    padding: 100px;
}
.round2 .page7::-webkit-scrollbar {
    width: 0px;
    height: 0px;
}
.round2 .page7 a{
    font-family: 'Acme';
    font-size: 30px;
    color: azure;
    text-shadow: 0 0 10px cyan;
}
.round2 .page7 p{
    color: aliceblue;
    text-shadow: 0 0 20px red, 0 2px 0 blue;
    font-size: 40px;
    font-family: 'Marcellus SC', serif;
}
.round2 .page7 .article{
    font-family: 'Acme', cursive;
    font-size: 30px;
    color: azure;
    text-shadow: 0 0 10px orange;
}
.round2 .page7 img{
    src: url('bck7.jpg');
    width: 550px;
    height: 550px;
}